h2 {
  font-size: 20px;
}
body {
  position: relative;
  display: inline-block;
  text-align: center;
  width: 100%;
  min-height: 100%;
  font-weight: 400;
  font-size: 16px;
}
  .action {
   background-color: #fff;
   &::after {
     z-index: -1;
     content: '';
     display: block;
     width: 34px;
     height: 34px;
     background-color: #fff;
     position: absolute;
     top: 7px;
     right: -17px;
     transform: rotate(45deg);
     box-shadow: 3px 3px 5px rgba(63, 62, 62, 0.199);
    }
  } 
.container {
  max-width: 991px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  overflow: hidden;
}
.container-right {
  margin-top: 50px;
}
.container-table {
   padding: 20px 30px;
  }
.forum-title {
  margin-left: 200px;
  li {
    padding: 5px 5px;
    border: 2px solid #eee;
    background-color: rgba(241, 209, 146, 0.815);
    width: 30%;
    display: inline-block;
  }
}
table{

  display: none;
  table-layout:fixed;
  margin-left: 200px;
  width: 77%;
  text-align: left;
  border: 1px solid rgba(241, 209, 146, 0.815);
}
tr {
  border-bottom: 1px #ccc dashed;
  background-color: #fff;
  // &:nth-child(odd){
  //   background-color: #fff;
  // }
}
td {
  width: 28%;
  overflow: hidden;
  text-overflow:ellipsis;
  word-break:keep-all;
  white-space:nowrap;
  display: inline-block;
  padding: 5px;
  a {
     display: block;
     overflow: hidden;
     text-overflow:ellipsis;
     span {
      margin-right: 10px;
     }
  }
}
.t-post {
  width: 41%;
}
.dplay {
  display: inline-table;
}
// 
.postfrom {
  margin-left: 235px;
  width: 72.5%;
  background-color: red;
  text-align: left;
  background-color: #eee;
  border: 1px solid rgba(241, 209, 146, 0.815);
  p {
    padding: 5px;
    background-color: #ccc;
  }
}
.post-select {
  padding: 5px;
  margin-top: 5px;
  input {
    width: 87.5%;
  }
}
.text-tar {
  padding: 5px;
  textarea {
    resize: none;
    width: 99.9%;
  }
}
.btns {
  padding: 5px;
}
@media screen and (max-width: 991px){
  
  header {
    display: none;
  }
  
  table {
    margin:0 auto;
    width: 80%;
  }
  
  .forum-title {
    margin: 0 auto;
    li {
      width: 25%;
    }
  }
  .postfrom {
    margin: 0 auto;
    width: 75%;
  }
  .post-select {
  input {
    margin-top: 5px;
    width: 87.5%;
  }
}
}
@media screen and (max-width: 658px){
  h2 {
    font-size: 16px;
  }
  table {
    width: 85%;
  }
  td {
    width: 30%;
    text-align: right;
  }
  .t-post {
    text-align: left;
    width: 30%;
  }
}
@media screen and (max-width: 400px){
  table {
    font-size: 14px;
    width: 100%;
  }
}